.movie-sort-link {
    
    ul {
        list-style-type: none;
        justify-content: center;
        text-align: center;
        align-items: center;
    }

    li {
        text-decoration: none;
        padding: 25px;
    }

    a {
        display: flex;
        font-size: 1.5em;
        text-align: center;
        justify-content: center;
        color: $main-colour;
        text-decoration: none;
    }

    a:hover {
        transition: 0.3s;
        padding-bottom: 5px;
        border-bottom: 5px solid $main-colour;
    }
}

.Main {
    display: flex;
    flex-direction: column;
}

.about {
    text-align: center;
    p {
        padding: 30px;
        text-align: justify;
    }
}

.outer-wrap {
    margin: 0;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    height: 100vh;
}

.favourites {
    padding: 0;

    p {
        text-align: center;
    }

}
