//Resets

//Box sizing reset
html {
    margin: 0;
    box-sizing: border-box;
  }
  *, *:before, *:after {
    box-sizing: inherit;
}
  
  body, nav, ul, h1, h2, p {
    margin: 0;
    padding: 0;
}

body {
  display: flex;
  flex-direction: column;
}

//Image reset
img {
    max-width: 100%;
    height: auto;
    display: block;
    margin: 0
}

.wrap {
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @media (min-width: 920px) {

    font-size: 0.8em;
  
    ul {
      display: flex;
      flex-direction: row;
    }

    li {
      font-size: 1.6em;
    }

    a {
      font-size: 2em;
    }

  }
  
}