header {
    background-color: $dark;
    
    h1 {
        text-align: center;
        margin: 10px;
        padding-bottom: 10px;
        transition: 0.5s;
        padding-bottom: 20px;
        color: white;
    }
}
nav {
    background-color: $dark;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    padding: 12px;

    ul {
        

        li {
            padding: 10px;
            list-style: none;
            flex: 1;
        }

    }



    a {
       text-decoration: none;
       color: white;
       transition: 0.5s;
       font-size: 20px;
    }

    a:hover {
       transition: 0.5s;
       color: $border-colour;
       padding-bottom: 5px;
       border-bottom: 5px solid $main-colour;
    }

    .cinewatch-search {
        display: flex;
        flex-direction: row;
    }

}


.website-title {
    a {
        text-decoration: none;
        h1 {
            color: #162640;
        }
    }
}

h1 {
    text-align: center;
    margin: 10px;
    transition: 0.5s;

    .title-color {
        color: $main-colour;
    }

}

h1:hover {
    color: $main-colour;
    transition: 0.5s;

}

.h2-header {
    text-align: center;
}

h2:after {
    content: ' ';
    display: block;
    border: 1px solid black;
    width: 75%;
    margin: 0 auto;
    margin-top: 10px;
}

input {
    padding: 6px;
}

button {
    margin: 0 auto;
}