html {
    background-color: $wrapper-bgc;
}

.wrapper {
    margin: 0 auto;
    background-color: white;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-grow: 1;

    .film-container{
        display: grid;
        grid-template-columns: repeat(2, 1fr);

        .box {
        border: 1px solid $border-colour;
        text-align: center;
        margin: 20px;
        position: relative;
        display: inline-block;

            @media (min-width: 768px){
                margin: 25px;
            }
        }

        .film-summary {
            z-index: 1;
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;
            font-size: .75em;
            background-color: rgba(255, 255, 255, 0.808);
            color: black;
            visibility: hidden;
            overflow:hidden;
            opacity: 0;
            transition: opacity 1s;
            .date {
                text-align: center;
                padding: 0;
            }

            .film-title {
                padding: 15px 10px;
                padding-bottom: 0px;
            }

            .more-info-tap {
                color: $main-colour;
                text-align: center;
                padding: 0;
                text-decoration: underline;

                a {
                    cursor: pointer;
                }

            }

            .more-info {
                display: none;
                text-align: center;
                text-decoration: underline;
            }

            p {
                color: $dark;
                font-size: 0.8em;
                text-align: left;
                padding-left: 10px;
                padding-right: 10px;
                line-height: 15px;
            }

            h4, h3 {
                margin: 0;
            }

            @media (min-width: 455px) {
                .more-info-tap {
                    padding-top: 5px;
                    padding-bottom: 10px;
                    margin-bottom: 10px;
                }
    
                p {
                    font-size: 1em;
                }
            }

            @media (min-width: 650px) {
                p {
                    font-size: 1.5em;
                    line-height: 25px;
                }
            }

            @media (min-width: 768px) {
                p {
                    font-size: 0.9em;
                    line-height: 18px;
                }
            }

            @media (min-width: 1700px) {
                p {
                    font-size: 1em;
                    line-height: 25px;
                }
            }

        }    
        .box:hover .film-summary {
            visibility: visible;
            opacity: 1;
        }

        button {
            text-align: center;
            border-radius: 10px;
            border: none;
            background-color: $dark;
            color: white;
            padding: 6px;
        }


        @media (min-width: 768px) {
            grid-template-columns: repeat(4, 2fr);
        }

        @media (min-width: 1200px) {
            grid-template-columns: repeat(5, 2fr);
            // .film-summary {
            //     position: initial;
            //     visibility: visible;
            //     opacity: 100%;
            // }
        }
    }

    .btn {
        color: whitesmoke;
        background-color: $main-colour;
        padding: 10px;
        border-radius: 10px;
    }

    @media (min-width: 768px) {
        ul {
            display: flex;
        }

    }

    @media (min-width: 920px) {
        p {
            font-size: 1.4em;
        }

        h1 {
            font-size: 4.5em;
        }
    }

}
