.single-page {

    .backdrop {
        display: none;
        width: 100%;
        height: auto;
        @media (min-width: 500px){
            display: block;
            position: relative;
        }
    }

    .overlay {
        
        .release-date {
            text-align: center;
        }
    }

    .overview-info {
        margin-top: 2em;
        margin-bottom: 5em;
        padding-left: 25px;
        padding-right: 25px;
        h1, h4, h3 {
            margin: 0;
        }

        h4 {
            text-align: center;
            padding-bottom: 2em;
        }

        p {
            padding-bottom: 20px;
        }

        .btn {
            margin-top: 20px;
        }
    }

    .poster {
        width: 455px;
        height: auto;
        @media (min-width: 500px){
            height: 487px;
            width: auto;
        }
    }

    a {
        text-align: center;
        text-decoration: none;
        color: rgb(48, 48, 48);
        font-size: 20px;
     }
 
    a:hover {
        transition: 0.5s;
        color: rgb(124, 155, 124);
    }

    .inner-wrapper {
        @media (min-width:500px) {
            display: flex;
            border: 1px solid rgb(228, 228, 228);
            padding: 5px;
        }
    }
}